import { Command, Query } from '@ali/4ever-cangjie';
import setLetterSpacing from './commands/setLetterSpacing';
import LetterSpacingMenu from './components/letterSpacingMenu';
import getLetterSpacing from './queries/getLetterSpacing';
import * as actions from './actions';
import onAction from './handlers/onAction';

const commands: Record<string, Command> = {
  setLetterSpacing,
};
const queries: Record<string, Query> = {
  getLetterSpacing,
};

function createLetterSpacingPlugin() {
  return {
    menubar: {
      menu: LetterSpacingMenu,
    },
    commands,
    queries,
    actions,
    onAction,
    models: {
      klass: 'mark',
      type: 'spacing',
    },
  };
}

createLetterSpacingPlugin.actions = actions;
createLetterSpacingPlugin.commands = commands;
createLetterSpacingPlugin.queries = queries;

export default createLetterSpacingPlugin;
